html {
  font-size: 62.5%;
}

body {
  font-family: $font-family-default;
  font-size: $font-size-default;
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: normal;
  color: $text-default;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  text-shadow: 0 6px 30px rgba(0, 0, 0, 0.5);
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
a {
  margin-top: 0;
  font-family: $font-family-default;
}

h1,
h2,
h3,
h4,
h5,
h6,
.heading--1,
.heading--2,
.heading--3,
.heading--4,
.heading--5,
.heading--6,
.display--1,
.display--2,
.display--3 {
  font-family: $font-family-heading;
}

h1,
.heading--1 {
  font-size: 4.8rem;
}
h2,
.heading--2 {
  font-size: 3.6rem;
}
h3,
.heading--3 {
  font-size: 3rem;
}
h4,
.heading--4 {
  font-size: 2.4rem;
}
h5,
.heading--5 {
  font-size: 2rem;
}
h6,
.heading--6 {
  font-size: 1.6rem;
}

.display--1 {
  font-size: 9rem;
  line-height: 1;
}
.display--2 {
  font-size: 7.2rem;
  line-height: 1;
}
.display--3 {
  font-size: 6rem;
  line-height: 1;
}

p,
a {
  margin-bottom: 0.6em;
}

li {
  margin-bottom: 0.2em;
}

strong {
  font-weight: 700;
}

pre {
  padding: 2rem;
  background-color: rgba(0, 0, 0, 0.1);
}

code {
  font-size: $font-size-default;
}

blockquote {
  margin: 0;
  padding: 0;
  padding-left: 20px;
  border-left: 2px solid $color-primary;
}

ul,
ol {
  margin: 0;
  padding: 0 20px;
}

a {
  color: $color-primary;
}

a:focus {
  outline: none;
}

.text-light {
  color: $text-light !important;
}
.text-default {
  color: $text-default !important;
}
.text-dark {
  color: $text-dark !important;
}
.text-primary {
  color: $color-primary !important;
}

.text-left {
  text-align: left !important;
}
.text-center {
  text-align: center !important;
}
.text-right {
  text-align: right !important;
}
.text-justify {
  text-align: justify !important;
}
.text-lowercase {
  text-transform: lowercase !important;
}
.text-uppercase {
  text-transform: uppercase !important;
}
.text-capitalize {
  text-transform: capitalize !important;
}

.wf-loading {
  visibility: hidden;
}
.wf-active {
  visibility: visible;
}

@media (max-width: $sm-max) {
  h1,
  .heading--1 {
    font-size: 3.6rem;
  }
  h2,
  .heading--2 {
    font-size: 3rem;
  }
  h3,
  .heading--3 {
    font-size: 2.4rem;
  }
  .display--1 {
    font-size: 6rem;
  }
  .display--2 {
    font-size: 4.8rem;
  }
  .display--3 {
    font-size: 4.2rem;
  }
  .lead {
    font-size: 1.8rem;
  }
  p {
    font-size: 1.8rem;
  }
}

.other-font {
  font-family: "Mouse Memoirs", sans-serif;
  font-size: 4rem;
}
